.table-style {
  img {
    vertical-align: text-bottom;
    margin-left: 10px;
  }
  color: #034a90;
  border-collapse: collapse;
  border-radius: 15px;
  border-style: hidden;
  td,
  th {
    border: 0;
    font-size: 16px;
    vertical-align: middle;
    text-align: center;
    font-weight: 500;
  }
  thead {
    tr {
      border-bottom: 2px solid $main-color;
    }
    th {
      font-size: 18px;
      font-weight: bold;
      text-align: center;
      border-bottom: 0;
      padding: 14px 0;
    }
  }
  tbody {
    td {
      font-size: 16px;
      text-align: center;
      border-top: 0;
      padding: 15px 0px;
      vertical-align: middle;
      font-weight: 400;
    }
  }

  tfoot {
    border-top: 2px solid #dee2e6;
    border-radius: 0 15px 0 15px;

    tr {
      background-color: #f8f8f8;
    }
    td {
      font-size: 18px;
      font-weight: bold;
      line-height: 20px;
      padding: 30px 0;
      border-left: 1px solid #c2c2c2;
      p {
        margin-bottom: 15px;
        font-size: 15px;
        font-weight: bold;
      }
    }

    .frist-column {
      border-bottom-right-radius: 15px;
    }
    .last-column {
      border-bottom-left-radius: 15px;
    }
  }
  ul {
    li {
      display: inline-block;
      &:first-child {
        margin-left: 15px;
      }
      a {
        text-decoration: underline;
      }
    }
  }
  i {
    font-size: 20px;
    font-weight: 500;
  }
}

.buttons-area {
  width: 500px;
  padding: 26px 0;
  display: flex;
  justify-content: space-between;
  align-items: baseline;
  & .btn-main:first-child {
    margin-left: 10px;
  }
}
.customer-name {
  a {
    color: $Yellow-color;
    text-decoration: underline;
  }
}
.operation-status {
  max-width: 103px;
  font-size: 16px;
  margin: auto;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  padding: 5px;
  border: 1px solid $gray;
}
//status
.confirmed-status {
  background-color: $lighter-green;
  border: 1px solid $green-color;
  color: $green-color;
}
.cancelled-status {
  background-color: $lighter-red;
  border: 1px solid $red-color;
  color: $red-color;
}
.open-status {
  background-color: $lighter-orange;
  border: 1px solid $orange-color;
  color: $orange-color;
}
.completed-status {
  background-color: $lighter-blue;
  border: 1px solid $main-color;
  color: $main-color;
}
//contracts-page
.contract-cases {
  text-align: right;
  a {
    margin: 0;
  }
  li {
    display: inline-block;
  }

  li:not(:last-child) {
    margin-left: 18px;
  }

  a {
    // display: block;
    margin: 0;
  }
}
.contract-number {
  width: 27px;
  height: 32px;
  padding: 5px;
  font-size: 16px;
  margin-right: 8px;
  // border:1px solid $light-gray;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
}
.view-all {
  font-size: 16px;
  color: $gray;
  text-decoration: underline;
  margin: 12px 0 0 56px;
  display: block;
  text-align: left;
}

.single-order {
  margin-top: 50px;
}
.client-box {
  border-radius: 15px;
  border: 1px solid #707070;
  padding: 25px;
  height: 100%;
  h5 {
    font-weight: bold;
    margin-bottom: 25px;
    color: #444444;

    span {
      margin-left: 15px;
      font-weight: bold;
    }
  }
}
.setting-list {
  a {
    text-decoration: none !important;
  }
  li:first-child {
    a {
      i:before {
        color: $gray;
      }
    }
  }
  li:nth-child(2) {
    margin-left: 18px;
    a {
      i:before {
        color: $red-color;
      }
    }
  }
}
.user-picture {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
}
.cancel{
  border: 2px solid $red-color;


}
.confirmed{
  border: 2px solid $main-color;


}
.opening{
  border: 2px solid $orange-color;


}
.completed{
  border: 2px solid $green-color;


}
