@import "_include-media.scss";

// datapicker

.input-group > .custom-select:not(:last-child),
.input-group > .form-control:not(:last-child) {
  border-top-right-radius: 12px !important;
  border-bottom-right-radius: 12px !important;
}
.input-group-text {
  background-color: transparent !important;
  border: none;
  padding: 0;
}
.fa-calendar:before,
.fa-clock-o:before {
  position: relative;
  left: 20px;
}
// autocomplete 
.ui-autocomplete  {
	z-index: 99999;
	border-radius:$main-border-radius;
	-webkit-border-radius:$main-border-radius;
	-moz-border-radius:$main-border-radius;
	-ms-border-radius:$main-border-radius;
	-o-border-radius:$main-border-radius;
	margin-top:  10px !important;
	padding: 15px 0;
	
}


.custom-autocomplete .ui-menu-item {
	padding: 4px;
	color: $main-color;
}

.custom-autocomplete .ui-menu-item:hover {
	cursor: pointer;
}
#ui-id-1 {
	top: 45px !important;
	
	
}

.ui-menu .ui-menu-item-wrapper {
	font-size: 20px;
	
}

.custom-autocomplete .ui-helper-hidden-accessible {
	display: none;
}
